<template>
	<div class="detail">
		<back></back>
		<div class="detail-content">

			<div class="title">
				<div class="top">
					<div class="img-box">
						<img :src="picUrl" alt="" width="100px">
					</div>
					<div class="text-box">
						<div class="desc"><span>{{desc}}</span></div>
						<div class="name"><span>{{name}}</span></div>
					</div>
				</div>
				<div class="bottom"><button><i class="iconfont icon-play1"></i>播放全部</button></div>
				
				<div class='fit'>
					<img :src="picUrl" alt="">
				</div>
				<div class='fiter'></div>
				
			</div>
			<div class="list"></div>
		</div>
	</div>
</template>

<script>
	import back from '@/base/back/back'
	export default {
		data (){
			return {
				name: '',
				picUrl: '',
				desc: '',
			} 
		},
		created (){
			// console.log(this.$route.query.name)
			this.name = this.$route.query.name
			this.picUrl = this.$route.query.picUrl
			this.desc = this.$route.query.desc
			
		},
		components: {
			back
		}
	}
</script>

<style scoped lang="stylus">
  @import "../../common/mixin"
  @import "../../common/icon.css"
	.detail
	  position: fixed
	  top: 0
	  bottom: 0
	  z-index: 20
	  width: 100%
	  background: $color-background
	  z-index: 500
	  & .detail-content
	  	height: 100%
	  	overflow: hidden
	  	& .title
	  	  color: #fff
	  	  position: relative
	  	  height: 40%
		  	& .top
		  	  z-index: 10
		  	  display: flex
		  	  position: absolute
		  	  top: 0
		  	  left: 0
		  	  width: 100%
		  	  padding-left: 12%
		  	  padding-right: 12%
		  	  padding-top: 10%
		  	  box-sizing: border-box
		  	  & .img-box
		  	  	width: 40%
		  	  	border-radius: .2em
		  	  	overflow: hidden
		  	  & .text-box
		  	  	flex: 1 
		  	  	text-align: left
		  	  	box-sizing: border-box
		  	  	padding: $padding
		  	  	display: flex
		  	  	flex-direction: column
		  	  	justify-content: space-between
		  	  	& .desc
		  	  	  height: 50%
		  	  	  font-size: $ll-font-size
		  	  	& .name
		  	  	  height: 50%
		  	  	  margin-top: 0rem
		  	  	  line-height: 400%
		  	& .fiter
		  	  background: rgba(50,50,50,.5)
		  	  width: 100%;
		  	  height: 100%
		  	  position: absolute
		  	  top: 0
		  	  left: 0
		  	  z-index: 1
		  	& .fit
		  	  overflow: hidden
		  	  height: 100%
		  	  width: 100%;
		  	  position: absolute
		  	  top: 0
		  	  left: 0
		  	& .fit img
		  	  filter: blur(10px)
		  	  width: 100%;
		  	  height: 100%
		  	  z-index: -10
		  	& .bottom 
		  	  z-index: 10
		  	  display: flex
		  	  position: absolute
		  	  bottom: 2rem
		  	  left: 0
		  	  color: #fff
		  	  width: 100%
		  	  text-align: center
		  	  & button
		  	  	color: $font-color
		  	  	width: 50%
		  	  	font-size: 1.1em
		  	  	height: 2.6rem
		  	  	background: transparent
		  	  	border-radius: 1.5rem
		  	  	borders($font-color)
		  	  	outline: none
		  	  	box-shadow: 1px 1px 1px rgba(0,0,0,0.2)
		  	  	margin: 0 auto
</style> 


