<template>
  <div style="position:fixed;
  top:75px;width:100%;left: 0;bottom:0">
    <scroll :datas="songList" :click="true" ref="scroll" style="height: 100%;overflow: hidden">
      <div class="home">
        <div>
          <slider :slider="this.slider" ref="slider"></slider>
        </div>
          <div>
            <div class="radio">
              <center v-text="radioTitle"></center>
              <div class="item-content">
                <div v-for="item in radio" class="item" :key="item.keys">
                  <div class="desc">
                    <div class="img-box">
                      <img :src="item.picUrl" alt="" width="100%">
                      <div class="desc">
                        <span><i class="iconfont icon-erji"></i>{{getNum(item.accessnum)}}</span>
                        <span><i class="iconfont icon-bofang"></i></span>
                      </div>
                    </div>
                  </div>
                  <div class="detail">
                    {{item.Ftitle}}
                  </div>
                </div>
                <br>
              </div>
            </div>
            <div class="songList">
              <center v-text="songTitle"></center>
              <div class="item-content" >
                <div v-for="item in songList" class="item" :key="item.keys" @click="toDetail(item.id,item.picUrl,item.songListAuthor,item.songListDesc)">
                  <div class="img-box">
                    <img :src="item.picUrl" alt="" width="100%">
                    <div class="desc">
                      <span><i class="iconfont icon-erji"></i>{{getNum(item.accessnum)}}</span>
                      <span><i class="iconfont icon-bofang"></i></span>
                    </div>
                  </div>
                  <div class="detail">
                    <div class="text">{{item.songListDesc}}</div>
                    <div class="author">{{item.songListAuthor}}</div>
                  </div>
                </div>
                <br>
              </div>
            </div> 
          </div> 
      </div>
    </scroll>
    <transition name="slide">
      <router-view class="detail-view"></router-view>
    </transition>
  </div>

</template>

<script>
  import {getRecommend,getSongList} from '@/api/recommend.js'
  import slider from '@/base/slider/slider'
  import scroll from '@/base/scroll/scroll'
    export default {
      data(){
        return {
          slider: [],
          radio: [],
          songList: [],
          radioTitle: '电台',
          songTitle: '热门推荐'
        }
      },
      created(){
        this._initSlider();
        getSongList().then(res => {
          console.log(res)
        })
      },
      methods: {
        _initSlider(){
          getRecommend().then(res =>{
            console.log(res)
            this.slider = res.data.slider ;
            this.radio = res.data.radioList;
            this.songList = res.data.songList
            setTimeout( () => {
              this.$refs.slider.play();
            },180)
          })   
        },
        getNum(num){
          num += "";
          return  num.length > 4?num.substring(0,3) + "万": num
        },
        toDetail(id,picUrl,name,desc){
          console.log('to')
          this.$router.push({path: "/home/detail",query: {
            id, 
            picUrl,
            name,
            desc,
            }
          });
        }
      },

      components: {
        slider,
        scroll
      }
    }
</script>

<style scoped lang="stylus">
  @import "../../common/mixin"
  @import "../../common/icon.css"
  br
    clear:both
  .home
    background: $color-background
    & .radio,& .songList
      width: 100%
      & center
        font-size: 1rem
        color: $font-color
        text-align: left
        padding: $padding
        text-shadow: 2px 2px 2px rgba(0,0,0,0.6)
      & .item-content
        width: 100%
        padding: $padding
        text-align: center
        box-sizing: border-box
        & .item
          width: 48%
          float: left
          height: auto
          font-size: 0
          margin-bottom: 1.2rem
          border-radius: 0.4rem
          overflow: hidden
          & .img-box
            position: relative
            box-shadow: 2px 2px 2px rgba(0,0,0,.5)
            & img
              display: block
            & .desc
              position: absolute
              bottom: 0
              left: 0
              width: 100%
              height: 1.6rem
              background: transparent
              color: #fff
              display: flex
              justify-content: space-between
              padding: $padding
              box-sizing: border-box
              & .icon-bofang
                font-size: 1.6rem
                margin-top: -1.5rem
                margin-right: -0.04rem
                display: inline-block
          & .detail
            height: 2.8rem
            background: rgba(255,255,255,.9)
            font-size: .9rem
            color: #333
            display: block
            text-align: left
            box-sizing: border-box
            padding: $padding
            & .text
              height: 1rem
              width: 100%
              overflow: hidden
              text-overflow: ellipsis
              font-size: $l-font-size
            & .author
              font-size: $s-font-size
              margin-top: 0.3rem
        & .item:nth-child(odd)
          margin-right: 4%
    & .songList
      width: 100%
      & .item div
        font-size: 0.1rem 
  .radio .icon-bofang
    margin-right: 0.2em !important
    margin-top: -0.6rem !important
  .radio .detail
    height: 2.2rem !important
  .slide-enter-active, .slide-leave-active
    transition: all 0.3s

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
    opacity: 0
</style>



