<template>
  <div id="app">
    <my-header></my-header>
    <my-nav></my-nav>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
  import myNav from '@/base/nav/nav'
  import myHeader from '@/base/header/header'

  export default {
    name: 'App',
    methods: {
      
    },
    components: {
      myNav,
      myHeader
    }
  }
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



