<template>
	<div><span class="back" @click="back()"><i class="iconfont icon-backoff"></i></span></div>
</template>

<script>
	export default {
		methods: {
			back(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style scoped lang="stylus">
	@import "../../common/icon.css"
	@import "../../common/mixin"
	& .back
		font-size: 2rem
		color: $font-color
		position: absolute
		top: 2%
		left: 5%
		display: block
		z-index: 200
</style>


