<template>
  <div class="wrapper">
    <slot></slot>
    <div class="blank"></div>
  </div>
</template>
<script>
  import Bscroll from 'better-scroll'
    export default {
        props: {
          datas: null,
          click: {
            type: Boolean,
            default: false
          },
          probeType: {
            type: Number,
            default: 1
          },
          listenScroll: {
            type: Boolean,
            default: false
          },
        },
        mounted() {
        },
        methods: {
          _initscroll(){
            this.scroll = new Bscroll('.wrapper', {
              click: this.click,
              probeType: this.probeType
            })
             
            if(this.listenScroll){
              let _this = this
              this.scroll.on('scroll', pos => {
                _this.$emit('scrollEnd', pos)

              })
            }
          },
          refresh(){
            this.scroll && this.scroll.refresh()
          },
          enable() {
            this.scroll && this.scroll.enable()
          },
          disable() {
            this.scroll && this.scroll.disable()
          },
        },
        watch: {
          datas(){
             
            if(this.scroll){
              this.refresh()
            }else {
                this._initscroll()
              
            }
          }
        }
    }
</script>

<!-- <style scoped lang="stylus">
  *
    margin: 0
    padding: 0
    list-style: none
  .wrapper
    width: 100vw
    position: fixed
    left:0
    bottom: 20px
    top: 75px
    overflow: hidden
    background: $color-background
</style> -->



