<template>
	
		<div class="search">
      <div class="search-box">
        <input type="text" v-model="hotValue" :placeholder="hotValueHolder" @keyup="search(1)">
        <i class="iconfont icon-close" @click="hotValue='' " v-show="hotValue!=''"></i>
      </div>
			<div class="search-hot">
        <h5 class="title">{{HOT_NAME}}</h5>
        <ul class="list">
          <li v-for="(key,index) in hotList" :key="index" @click="fillKey(key.k)">
            {{key.k}}
          </li>
        </ul> 
      </div>
      <transition name="search">
        <div class="search-list" v-show="searchListShow" ref="wrappers">
          <loading v-show="searchData.length == 0"></loading>
          <scroll 
          :datas="searchData" 
          :click="true" 
          @scrollEnd="scrollEnd" 
          :listenScroll="true" 
          :probeType="3" 
          ref="scroll"
          style="height: 100%;overflow: hidden;">
            <div>
              <ul ref="list">
                <li v-for="(song,index) in searchData" class="song" :key="index">
                  <div class="songname">{{song.songname}}</div>
                  <div class="singername">{{song.singer[0].name}} <span v-show="song.albumname.length!=0">《{{song.albumname}}》</span> </div>
                </li>
              </ul>
            </div>
          </scroll>
          <div v-show="isGetMore" style="text-align: center;color: #ffcc33">loading...</div>
        </div>
      </transition> 
		</div>
	
</template>

<script>
  import {getHotKey,search} from '@/api/search.js'
  import scroll from '@/base/scroll/scroll'
  import loading from '@/base/loading/loading'
  const PERNUM = 30  //每页数量 

	export default {
    data() {
      return {
        hotValue: '',
        hotValueHolder: '请输入要搜索的歌手或歌曲',
        HOT_NAME: "热门搜索",
        hotList: [],
        searchData: [],
        curpage: 1,  //当前页数
        searchListShow: false,
        total: 0,
        isGetMore: false
      }
    },
    created(){
      this.getHotKey()
    },
    methods: {
      getHotKey(){
        getHotKey().then( res => {
          this.hotList = res.data.hotkey.slice(0,15)
        })
      },
      
      search(type){
        if(this.hotValue.length == 0){
          return
        }
        this.searchListShow = true
        search(this.hotValue,this.curpage,PERNUM).then( res => {
          if(type){
            this.curpage = 1
            this.searchData = []
          }
          this.searchData = this.searchData.concat(res.data.song.list);
          this.total = res.data.song.totalnum
          console.log(res.data)
          setTimeout( () => {
             this.isGetMore = false
             this.$refs.scroll.enable()
             this.$refs.scroll.refresh()
          },100)
        })
      },
      fillKey(key){
        this.searchData = []
        this.searchListShow = true
        this.hotValue = key
        setTimeout( () => {
          this.search()
        },100)
      },
      scrollEnd(pos) {
        let wrapperHeight = this.$refs.wrappers.offsetHeight - 30;
        let listHeight = this.$refs.list.offsetHeight
        if(wrapperHeight - pos.y >= listHeight) {
          if(this.curpage * PERNUM < this.total){
            if(!this.isGetMore){
              this.isGetMore = true
              this.curpage ++;
              this.search();
              this.$refs.scroll.disable() 
            }
          }else {
            console.log("没了")
          }
        }
      }
    },
    watch:{
      hotValue(){
        setTimeout( () => {
          if(this.hotValue.length == 0){
            this.searchListShow = false
          }
        },100)
      },
    },
    components: {
      scroll,
      loading
    }
	}
</script>

<style scoped lang="stylus">
  @import "../../common/mixin"
  @import "../../common/icon.css"
  .search
    position:fixed
    top:75px
    bottom: 0
    width: 100%
    left: 0
    background: $color-background
    & .search-box
      height: 44px
      padding: $padding
      padding-top: 20px !important
      position: relative
      & i
        font-size: 20px
        color: #444
        position: absolute
        right: 20px
        top: 34px
      & input
        color: #eee
        width: 100%
        height: 32px
        noborder()
        background: #999
        border-radius: 16px
        padding: 0 18px
        box-sizing: border-box
        margin-top: 9px
        font-size: $l-font-size
    & .search-hot
      text-align: left
      padding: $padding
      & .title
        height: 40px
        color: #aaa
        font-size: $ll-font-size
        padding-left: 8px
        line-height: 40px
      & .list
        font-size: $l-font-size
        color: #ddd
        & li
          display: inline-block
          padding: 8px 12px
          background: #333
          margin: 6px
          border-radius: 12px
    & .search-list
      position: fixed
      top: 138px
      width: 100%
      bottom: 20px
      background: #444
      color: #ddd
      padding-top: 30px !important
      text-align: left
      padding: $padding
      box-sizing: border-box
      & ul
        background: #444
        & .song
          height:44px
          border-bottom: 1px solid #666
          padding: $padding
          & .singername
            font-size: $s-font-size
            color: #aaa
            width:100%
            text-overflow: clip
            overflow: hidden
            height: 16px
            line-height: 16px
          & .songname
            font-size: $ls-font-size
            height: 24px
            color: #fff
            text-overflow: clip
            line-height: 24px
            overflow: hidden
/*  .search-enter-active, .search-leave-active
    transition: all 0.3s

  .search-enter, .search-leave-to
    /*transform: translate3d(10, 0, 100%)
    opacity: 0*/

</style>




