<template>
    <div class="rank">
        <scroll :datas="topList" :click="true" style="overflow: hidden;height: 100%">
            <div class="rank-item">
                <ul>
                    <li v-for="item in topList" :key="item.index" class="item" @click="toList(item.id)">
                        <div>
                            <img :src="item.picUrl" alt="" width="100" height="100">
                        </div>
                        <div class="song-list">
                            <span v-for="(song,index) in item.songList" :key="index">
                                <i class="iconfont icon-guanjun" v-if="index +1 == 1"></i>
                                <i class="iconfont icon-yajun" v-else-if="index +1 == 2"></i>
                                <i class="iconfont icon-jijun" v-else-if="index +1 == 3"></i>
                                {{song.songname}}-{{song.singername}}
                            </span>
                        </div>
                    </li>
                </ul>
            </div>
        </scroll>
        <transition name="slide">
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
    import scroll from '@/base/scroll/scroll'
    import {getTopList} from '@/api/rank'

    export default {
        data(){
            return {
                topList: []
            }
        },
        created(){
            this.getTopList()
        },
        methods:{
            getTopList(){
                getTopList().then( res => {
                    console.log(res)
                    this.topList = res.data.topList
                })
            },
            toList(id) {
                this.$router.push({path: '/rank/' + id})
            }
        },
        components: {
            scroll,
           
        }
    }
</script>

<style lang="stylus" scoped>
  @import "../../common/mixin"
  @import "../../common/icon.css"

    .rank
        position: fixed;
        top: 75px
        left: 0
        bottom: 0
        color: #ccc
        & .rank-item
            padding: $padding
            width: 100vw
            box-sizing: border-box
            & .item
                display: flex
                width: 100%
                margin: 1rem .75em  .75rem 0
                box-sizing: border-box
                height: 100px
                background: #445
                & .song-list
                    padding: .75rem 0 0 .75rem
                    text-align: left
                    margin: auto 1.1rem
                    font-size: 0.85rem
                    padding-top: .225rem
                    overflow: hidden
                    box-sizing: border-box
                & .song-list span
                    display:block
                    margin-top: .5rem
                    margin-bottom: .5rem
                    white-space: nowrap;
                    text-overflow: ellipsis
                    overflow: hidden
    .rank-item ul
        overflow: hidden
.slide-enter-active, .slide-leave-active
    transition: all 0.3s

.slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
</style>



