<template>
	<div class="detail">
		<loading v-show="loadingShow"></loading>
		<back class="back"></back>
		<div class="detail-content">
			<h5 class="top">{{singerName}}</h5>
			<div class="titles" ref="photo">
				<transition name="fade">
					<div class="bottom" v-show="buttonShow"><button><i class="iconfont icon-play1"></i>播放全部</button></div>
				</transition>
				<div class='fit'>
					<img :src="picUrl" alt="" width="100%" height="auto">
				</div>
			</div>
	 			<div class="bg-layer" ref="layer" style="overflow:hidden"></div>
				<div  class="wrappers">
					<div style="height: 100%" class="scroll">
							<div class="list">
								<ul>
									<li v-for='(item,index) in songlist' :key="index">
										<div class="title">
											<span>{{item.musicData.songname}}</span>
										</div>
										<div class="singer-albumn">
											<span>{{item.musicData.singer[0].name}}</span>
											<span>{{item.musicData.albumname}}</span>
										</div>
									</li>
								</ul>
							</div>
					</div>
				</div>	
		</div>
	</div>
</template>

<script>
	import back from '@/base/back/back'
	import loading from '@/base/loading/loading'
	import {getSingerDetail} from '@/api/singer'
	import Bscroll from 'better-scroll'

	const AVATAR_URL1 = 'https://y.gtimg.cn/music/photo_new/T001R300x300M000'
	const AVATAR_URL2 = '.jpg?max_age=2592000'

	export default {
		data (){
			return {
				picUrl: '',
				songlist: [],
				singerName:'',
				buttonShow: true,
				loadingShow: true
			} 
		},
		created (){
		 	
		 	this.picUrl = AVATAR_URL1+this.$route.params.id+AVATAR_URL2
		 	this.getSingerDetail(this.$route.params.id)
		},
		methods: {
			getSingerDetail(id){
				getSingerDetail(id).then(res => {
					console.log(this.songlist)
					this.singerName = res.data.singer_name
					this.songlist = res.data.list
					this.$nextTick( ()=> {
						this.loadingShow = false
						console.log(this.loadingShow)
						this.scrolls = new Bscroll('.scroll', {
							click: true,
							probeType: 3,
						});
						let _this = this;
						this.scrolls.on('scroll', pos => {
							let height = this.$refs.photo.offsetHeight
							let translateY = Math.max(-250, pos.y)
							this.$refs.layer.style.transform = `translate3d(0,${translateY}px,0)`
							if(pos.y > 0) {
								this.$refs.photo.style.transform = `scale(${pos.y/height/1.5 +1})`
							}else {
								if(-pos.y > 30) {
									this.buttonShow = false
								}else {
									this.buttonShow = true
								}
								if(-pos.y > 302) {
									document.querySelector('.top').style.background ='rgba(0,0,0,0.7)'
								}else {
									document.querySelector('.top').style.background = 'transparent'
								}
							}
						})
						//下拉动作
					})


				})
			}
		},
		components: {
			back,
			loading
		}
	}
</script>

<style scoped lang="stylus">
  @import "../../common/mixin"
  @import "../../common/icon.css"
	.detail
	  position: fixed
	  top: 0
	  bottom: 0
	  z-index: 20
	  width: 100%
	  background: $color-background
	  z-index: 500
	  & .back
	  	z-index: 999
	  & .top
  	  	z-index: 10
  	  	position: absolute
  	  	top: 0
  	  	left: 0
  	  	width: 100%
  	  	text-align: center
  	  	font-size: $ll-font-size
  	  	padding-top: 1em
  	  	height: 44px
  	  	z-index: 888
  	  	color: #fff
	  & .detail-content
	  	height: 100%
	  	overflow: hidden
	  	& .titles
	  	  color: #fff
	  	  position: relative
	  	  height: 305px
	  	  top: 0
	  	  & .img-box
	  	  	width: 40%
	  	  	border-radius: .2em
	  	  	overflow: hidden
	  	  	& .desc
	  	  	  height: 50%
	  	  	  font-size: $ll-font-size
	  	  	& .name
	  	  	  height: 50%
	  	  	  margin-top: 0rem	
		  	& .fit img
		  	  width: 100%;
		  	  height: 100%
		  	  z-index: -10
		  	& .bottom 
		  	  z-index: 10
		  	  display: flex
		  	  position: absolute
		  	  bottom: 30px
		  	  left: 0
		  	  color: #fff
		  	  width: 100%
		  	  text-align: center
		  	  & button
		  	  	color: $font-color
		  	  	width: 50%
		  	  	font-size: 1.1em
		  	  	height: 2.6rem
		  	  	background: transparent
		  	  	border-radius: 1.5rem
		  	  	borders($font-color)
		  	  	outline: none
		  	  	box-shadow: 1px 1px 1px rgba(0,0,0,0.2)
		  	  	margin: 0 auto
		& .list
		  color: #fff
		  background: #444
		  padding: 20px 8% 20px 8%
		  padding-top: 20px
		  text-align: left
		  & li 
		  	margin: 14px 0
		  & .title
		    color: #fff
		    font-size: 1rem
		  & .singer-albumn
		    color: #999
		    font-size: $s-font-size
		    margin-top: 3px
.wrappers
  position:absolute
  top: 305px
  width:100%
  left: 0
  bottom:0
  z-index: 300
  transform: translateY(0)
.bg-layer
	position: relative
	height: 100%
	background: $color-background
.fade-enter-active, .fade-leave-active
	transition: all 0.4s

.fade-enter, .fade-leave-to
	
	opacity: 0	
</style> 


