<template>
  <div style="position:fixed;
  top:75px;width:100%;left: 0;bottom:0">
		<scroll :datas="dataList" :click="true" style="height: 100%;overflow: hidden">
			<div class="singer">
				<ul v-for="(list,idx) in dataList" :key="idx" class="singer-list" >
					<h4 id="list.title">{{list.title}}</h4>
					<li v-for="(item,index) in list.items" :key="index" class="singer-list-item" @click="showDetail(item.id)">
						<div><img :src="item.avatar" width="50" height="auto" ></div>
						<div class="name">{{item.name}}</div>
					</li>
				</ul>
			</div>
		</scroll>
		<ul class="bar">
			<li class="bar-list" v-for="(list,index) in dataList" :key="index">
				<a :href="list.title">{{list.title}}</a>
			</li>
		</ul>
		<transition name="slide">
	      <router-view class="detail-view"></router-view>
	    </transition>
	</div>
</template>

<script>
	import scroll from '@/base/scroll/scroll'
	import {getSingerList,getSingerDetail} from '@/api/singer.js'

	const HOT_LENGTH = 10
	const HOTNAME = '热门'
	const AVATAR_URL1 = 'http://y.gtimg.cn/music/photo_new/T001R150x150M000'
	const AVATAR_URL2 = '.jpg?max_age=2592000'
	export default {
		data(){
			return {
				dataList: [],
			}
		},
		created(){
			getSingerList().then( (res) => {
				//console.log(this.normalRiseList(res.data.list))
					
				this.dataList = this.normalRiseList(res.data.list)
				console.log(this.dataList)
			})	
		},
		methods: {
			normalRiseList(obj){
				let map = {
					hot: {
						title: HOTNAME,
						items: []
					}
				}
				for(let i in obj){
					if(i < HOT_LENGTH){
						let item = {
							name: obj[i].Fsinger_name,
							id: obj[i].Fsinger_mid,
							avatar: AVATAR_URL1 + obj[i].Fsinger_mid + AVATAR_URL2
						}
						map.hot.items.push(item)
					};

			        const key = obj[i].Findex
			        if (!map[key]) {
			            map[key] = {
			              title: key,
			              items: []
			            }
			        }
					map[key].items.push({
						name: obj[i].Fsinger_name,
						id: obj[i].Fsinger_mid,
						avatar: AVATAR_URL1 + obj[i].Fsinger_mid + AVATAR_URL2
					})
				}
				//序列化列表
				// console.log("map")
				// console.log(map)
				let hot = [];
				let list = [];
				for(let i in map){
					if(map[i].title.match(/[a-zA-Z]/)){
						list.push(map[i])
					}else if(map[i].title === HOTNAME){
						hot.push(map[i])
					}
				}
				list.sort( (a,b) => {
					return a.title.charCodeAt(0) - b.title.charCodeAt(0)
				})
				return hot.concat(list)
			},
			showDetail(id){
				this.$router.push({path: `/singer/${id}`})
			}
		},
		components: {
			scroll
		}
	}
</script>

<style scoped lang="stylus">
  @import "../../common/mixin"
.bar
  position: fixed
  right: 1.2em
  top: 50%
  color: #ccc
  width: 20px
  text-overflow: hidden
  overflow: hidden
  transform: translateY(-42%)
  padding: 12px 0
  border-radius: 10px
  background: rgba(0,0,0,.3)
  z-index: 300
  .bar-list
  	width: 100%
  	height: 18px
  	text-overflow: hidden
  	overflow: hidden
.singer
  font-size: $l-font-size
 & .singer-list
  color: #ddd
  text-shadow: 2px 2px 2px rgba(0,0,0,.5)
  & h4
  	width: 100%
  	height: 26px
  	line-height: 27px
  	background: #333
  	text-align: left
  	box-sizing: border-box
  	padding: 0 6%
  	font-size: $l-font-size
	& .singer-list-item
	  display: flex
	  height: 62px
	  line-height: 62px
	  padding: 0 8%
	  & .name
	  	margin-left: 2rem
	  & img
	  	border-radius: 50%
	  	vertical-align: top
	  	margin-top: 6px
  .slide-enter-active, .slide-leave-active
    transition: all 0.3s

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)
    opacity: 0	
</style>


